<template>
  <b-container>
    <b-row>
      <b-col>
        <form-wizard
          @on-complete="save"
          @on-error="handleErrorMessage"
          error-color="#a94442"
          shape="square"
          color="#49a7c1"
          subtitle="Preencha seus dados corretamente e participe da XI SACSIS"
          stepSize="lg"
          finishButtonText="ENVIAR"
          nextButtonText="PRÓXIMO"
          backButtonText="VOLTAR"
        >
          <!-- sobrescrita do título -->
          <h2 slot="title" class="text-uppercase">{{title}}</h2>
          <!-- ./sobrescrita do título -->
          <tab-content title="Sobre você" icon="fa fa-user" :before-change="checkForm">
            <!-- personal content -->
            <b-form>
              <b-form-group id="input-group-1" label="Nome Completo:" label-for="input-1">
                <b-form-input
                  id="input-1"
                  type="email"
                  required
                  v-model="teacher.name"
                  placeholder="Conte-nos o seu nome"
                />
              </b-form-group>
              <b-form-group id="input-group-2" label="Resumo:" label-for="input-2">
                <b-form-textarea
                  id="input-2"
                  placeholder="Fale um pouco sobre você..."
                  no-resize
                  no-auto-shrink
                  rows="3"
                  max-rows="6"
                ></b-form-textarea>
              </b-form-group>
              <b-form-group
                id="input-group-2"
                label="RG:"
                label-for="input-2"
                description="Apenas números"
              >
                <b-input v-model="teacher.rg" type="number" placeholder="RG" required/>
              </b-form-group>
              <b-form-group id="input-group-3" label="CPF:" label-for="input-3">
                <the-mask
                  v-model="teacher.cpf"
                  placeholder="CPF"
                  class="form-control"
                  required
                  :mask="['###.###.###-##']"
                />
              </b-form-group>
              <b-form-group
                id="input-group-8"
                label="Foto:"
                label-for="input-8"
                description="Formatos aceitos: .jpg, .jpeg e .png"
              >
                <b-form-file
                  id="input-8"
                  v-model="teacher.avatar"
                  browse-text="Escolher"
                  placeholder="Escolha um arquivo..."
                  drop-placeholder="Solte o arquivo aqui..."
                ></b-form-file>
              </b-form-group>
              <b-form-group id="input-group-4" label="Facebook:" label-for="input-4">
                <b-input-group>
                  <b-input-group-text slot="prepend">
                    <i class="fab fa-facebook" style="font-size: 1rem;"/>
                  </b-input-group-text>
                  <b-input id="input-4" placeholder="@usuario"/>
                </b-input-group>
              </b-form-group>
              <b-form-group id="input-group-5" label="Instagram:" label-for="input-5">
                <b-input-group>
                  <b-input-group-text slot="prepend">
                    <i class="fab fa-instagram" style="font-size: 1rem;"/>
                  </b-input-group-text>
                  <b-input id="input-5" placeholder="@usuario"/>
                </b-input-group>
              </b-form-group>
              <b-form-group id="input-group-6" label="Twitter:" label-for="input-6">
                <b-input-group>
                  <b-input-group-text slot="prepend">
                    <i class="fab fa-twitter" style="font-size: 1rem;"/>
                  </b-input-group-text>
                  <b-input id="input-6" placeholder="@usuario"/>
                </b-input-group>
              </b-form-group>
              <b-form-group id="input-group-7" label="Site:" label-for="input-7">
                <b-input-group prepend="www." class="mt-3">
                  <b-input id="input-7" placeholder="seusite.com.br"/>
                </b-input-group>
              </b-form-group>
            </b-form>
            <!-- ./personal content -->
          </tab-content>
          <tab-content title="Sobre a Minicurso" icon="fa fa-chalkboard-teacher">
            <!-- course details -->
            <b-form>
              <b-form-group id="input-group-9" label="Título:" label-for="input-9">
                <b-input placeholder="Título do minicurso" id="input-9"/>
              </b-form-group>
              <b-form-group id="input-group-10" label="Conteúdo:" label-for="input-10">
                <b-form-textarea
                  id="input-10"
                  placeholder="O minicurso irá abordar..."
                  no-resize
                  v-model="teacher.conteudo"
                  no-auto-shrink
                  rows="3"
                  max-rows="6"
                ></b-form-textarea>
              </b-form-group>
            </b-form>
            <!-- ./teacher details -->
          </tab-content>
          <tab-content title="Confirmar Dados" icon="fas fa-clipboard-check">
            <!-- confirmacao -->
            <b-form>
              <b-form-group id="input-group-1" label="Nome Completo:" label-for="input-1">
                <b-form-input
                  v-model="teacher.nome"
                  id="input-1"
                  readonly
                  type="email"
                  placeholder="Conte-nos o seu nome"
                />
              </b-form-group>
              <b-form-group id="input-group-2" label="Resumo:" label-for="input-2">
                <b-form-textarea
                  v-model="teacher.resumo"
                  readonly
                  id="input-2"
                  placeholder="Fale um pouco sobre você..."
                  no-resize
                  no-auto-shrink
                  rows="3"
                  max-rows="6"
                ></b-form-textarea>
              </b-form-group>
              <b-form-group label="Telefone:">
                <b-input type="tel" placeholder="Telefone" readonly v-model="teacher.telefone"/>
              </b-form-group>
              <b-form-group id="input-group-3" label="CPF:" label-for="input-3">
                <the-mask
                  v-model="teacher.cpf"
                  placeholder="CPF"
                  class="form-control"
                  readonly
                  :mask="['###.###.###-##']"
                />
              </b-form-group>
              <b-form-group id="input-group-2" label="RG:" label-for="input-2">
                <b-input v-model="teacher.rg" readonly type="number" placeholder="RG"/>
              </b-form-group>
            </b-form>
            <b-form-group id="input-group-9" label="Título:" label-for="input-9">
              <b-input
                v-model="teacher.titulo"
                placeholder="Título do minicurso"
                id="input-9"
                readonly
              />
            </b-form-group>
            <b-form-group id="input-group-10" label="Conteúdo:" label-for="input-10">
              <b-form-textarea
                id="input-10"
                v-model="teacher.conteudo"
                readonly
                placeholder="O minicurso irá abordar..."
                no-resize
                no-auto-shrink
                rows="3"
                max-rows="6"
              ></b-form-textarea>
            </b-form-group>
            <!-- ./confirmacao -->
          </tab-content>
          <div v-if="errorMsg">
            <span class="error">{{errorMsg}}</span>
          </div>
        </form-wizard>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import axios from "axios";
import { baseApiUrl, showError, showSuccess } from "@/global";

export default {
  props: ["token"],
  name: "FormTeacherSignup",
  data() {
    return {
      errorMsg: null,
      teacher: {
        nome: "",
        resumo: ""
      }
    };
  },
  computed: {
    title() {
      return this.teacher.nome ? `OLÁ ${this.teacher.nome}` : "OLÁ MINISTRANTE";
    }
  },
  methods: {
    save() {
      let parsedTeacher = JSON.parse(JSON.stringify(this.teacher));
      const data = {
        type_form: "course",
        nome: parsedTeacher.nome,
        resumo: parsedTeacher,
        rg: parsedTeacher.rg,
        cpf: parsedTeacher.cpf,
        avatar: parsedTeacher.avatar,
        facebook: parsedTeacher.facebook,
        twitter: parsedTeacher.twitter,
        instagram: parsedTeacher.instagram,
        site: parsedTeacher.site,
        email: parsedTeacher.email,
        telefone: parsedTeacher.telefone,
        titulo: parsedTeacher.titulo,
        conteudo: parsedTeacher.conteudo
      };
      axios
        .post(`${baseApiUrl}/speaker/?token=${this.token}`, data)
        .then(res => {
          if (res.status === 200) {
            showSuccess(res.data.message);
          } else {
            showError(res.data.message);
          }
        });
    },
    checkForm() {
      this.errors = [];

      return new Promise((resolve, reject) => {
        if (!this.teacher.nome) {
          let msg = "Nome é obrigatório";
          this.errors.push(msg);
          showError(msg);
        } else if (!this.teacher.resumo) {
          let msg = "Resumo é obrigatório";
          this.errors.push(msg);
          showError(msg);
        } else if (!this.teacher.rg) {
          let msg = "RG é obrigatório";
          this.errors.push(msg);
          showError(msg);
        } else if (!this.teacher.cpf) {
          let msg = "CPF é obrigatório";
          this.errors.push(msg);
          showError(msg);
        } else if (!this.teacher.email) {
          let msg = "E-mail é obrigatório";
          this.errors.push(msg);
          showError(msg);
        } else if (!this.teacher.telefone) {
          let msg = "Telefone é obrigatório";
          this.errors.push(msg);
          showError(msg);
        } else if (!this.teacher.titulo) {
          let msg = "Título do minicurso é obrigatório";
          this.errors.push(msg);
          showError(msg);
        } else if (!this.teacher.conteudo) {
          let msg = "Conteúdo do minicurso é obrigatório";
          this.errors.push(msg);
          showError(msg);
        } else if (!this.teacher.avatar) {
          let msg = "Foto é obrigatório";
          this.errors.push(msg);
          showError(msg);
        }

        if (!this.errors.length) {
          resolve(true);
        } else {
          reject("Preencha os campos obrigatórios!");
        }
      });
    },
    handleErrorMessage(errorMsg) {
      this.errorMsg = errorMsg;
    }
  }
};
</script>

<style scoped>
span.error {
  color: #e74c3c;
  font-size: 20px;
  display: flex;
  justify-content: center;
}
</style>